import { endpoints } from '../config'
/**
 * NOTEL this is mirrored on the server. MAKE SURE YOU UPDATE IT IN BOTH PLACES
 */
export const createShareUrl = (series, photoId, omitRoot = false) => {
  const urlSuffix = `/${series.id}/${photoId}`
  const url = omitRoot ? urlSuffix : `${window.location.origin}${process.env.PUBLIC_URL}${urlSuffix}`

  const ogShare = `${endpoints.share}/${series.id}/${photoId}`
  console.log('SOCIAL URL', { url, ogShare })
  return { url, ogShare }
}

export const decodeShareUrl = (pathName) => {
  try {
    const [sid, pid] = pathName.split('/').filter(s => s)
    return { type: 'series', sid, pid }
  }
  catch (err) {
    console.error(err)
  }
  return null
}
